import { request } from 'utils';

const prefix = '/api/students';

export const getEnrollSolutions = ({ page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createEnrollSolutions = ({
  id,
  label,
  courseId,
  areaids,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
  reviewTypeIds,
  yearEduId,
}) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    data: {
      id,
      label,
      courseId,
      areaids,
      minTotalPoint,
      minPointPerSubject,
      noOfStudent,
      reviewTypeIds,
      yearEduId,
    },
    method: 'POST',
  });

export const deleteEnrollSolutions = ({ id }) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    method: 'DELETE',
    data: { id },
  });

export const getEnrollSolutionsById = ({ id }) =>
  request({
    prefix,
    url: `/EnrollSolution/GetById`,
    method: 'GET',
    params: { id },
  });

export const updateEnrollSolutions = ({
  id,
  label,
  courseId,
  areaids,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
  reviewTypeIds,
  yearEduId,
}) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    data: {
      id,
      label,
      courseId,
      areaids,
      minTotalPoint,
      minPointPerSubject,
      noOfStudent,
      reviewTypeIds,
      yearEduId,
    },
    method: 'PUT',
  });



