import React, { Component } from 'react';
import { Card, Row, Col, message } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getEnrollSolutions, deleteEnrollSolutions } from 'services/enrollment';

class ListEnrollmentPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
    };
  }

  handleDelete = async id => {
    await deleteEnrollSolutions({ id });
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Mã phương án',
        dataIndex: 'id',
        align: 'center',
        key: 'id',
      },
      {
        title: 'Tên phương án',
        dataIndex: 'label',
        align: 'center',
        key: 'label',
      },
      {
        title: 'Khu vực tuyển sinh',
        dataIndex: 'areas',
        align: 'center',
        key: 'areas',
      },
      {
        title: 'Xét theo',
        dataIndex: 'reviewTypes',
        align: 'center',
        // render: (text, record) => {
        //   if (record.reviewTypeId === 1) {
        //     return <div>Học bạ</div>;
        //   }
        //   if (record.reviewTypeId === 2) {
        //     return <div>Kỳ thi THPT</div>;
        //   }
        //   return <div />;
        // },
      },
      {
        title: 'Khóa',
        dataIndex: 'courseName',
        align: 'center',
        key: 'courseName',
      },
      {
        title: 'Năm học',
        dataIndex: 'yearEduName',
        align: 'center',
        key: 'yearEduName',
      },
      {
        title: 'Điểm tổng',
        dataIndex: 'minTotalPoint',
        align: 'center',
        key: 'minTotalPoint',
      },
      {
        title: 'Điểm chi tiết',
        dataIndex: 'minPointPerSubject',
        align: 'center',
        key: 'minPointPerSubject',
      },
      {
        title: 'Số lượng tuyển sinh',
        dataIndex: 'noOfStudent',
        align: 'center',
        key: 'noOfStudent',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        width: '100px',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.TUYEN_SINH_NHAP_HOC__PHUONG_AN_TUYEN_SINH__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <Link to={routeConstant.TUYEN_SINH_NHAP_HOC__PHUONG_AN_TUYEN_SINH__NHAP_DU_LIEU}>
                <BtnAddNew />
              </Link>
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getEnrollSolutions} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListEnrollmentPlan />
  </Page>
);



